import './Hero.css';
import Button from '../../components/Button/Buttons'; 
import { buttonHero } from '../../config/buttonConfig'; 
import { useScroll } from '../../utils/useScroll';
import { socialInfo } from '../../components/Social/SocialInfo.js';
import Social from '../../components/Social/Social.js';



function Hero () {
    const {toSection} = useScroll();
    return (
        <section className="hero" id="sobre">
            <div className="container-hero">
                <div className="container-apresentacao">
                    <h1>Ol, eu sou <strong>Matheus Luna</strong></h1>
                    <h2>Desenvolvedor Back-End Java</h2>
                    <p>
                        Desenvolvedor Back-End, atuo no desenvolvimento de APIs REST modernas, seguras e escalveis com Java e Spring Boot, aplicando boas prticas de arquitetura, autenticao JWT e persistncia com JPA. 
                        Possuo conhecimento em JavaScript, React, HTML e CSS para integrao com o front-end 
                        e estudo Inteligncia Artificial e integrao com APIs da OpenAI, sempre buscando transformar ideias em solues funcionais e reais.
                    </p>
                </div>
                <div className="container-links">
                    {buttonHero.map((btn, index) => (
                        <Button
                            key={index}
                            text={btn.text}
                            variant={btn.variant}
                            onClick={() => toSection(btn.target)}
                        />
                    ))}
                </div>
                <div className="container-redes">
                    {socialInfo.map((a, index) => (
                        <Social
                            key={index}
                            icon={a.icon}
                            link={a.link}
                        />
                    ))}
                </div>
            </div>
            <div className="container-img">
                <div class="blur-effect"></div>
                <img src="img/matheusluna (2).png" className="hero-img"></img>
            </div>
        </section>
    );
}

export default Hero;